import React from 'react';
import './BurgerControl.scss'

import BuildControl from './BuidControl/BuildControl'


const controls = [
    {label: 'Cheese', type: 'cheese', price: 3},
    {label: 'Salad', type: 'salad', price: 2},
    {label: 'Meat', type: 'meat', price: 5},
]

const BurgerControl = (props) => {
    let numberIngredients = Object.keys(props.ingredients).map(igkey => {
        let imgKey;
        if(igkey === 'cheese') {
            imgKey = 'cheese'
        }
        if(igkey === 'meat') {
            imgKey = 'meat'
        }
        if(igkey === 'salad') {
            imgKey = 'salad'
        }
        return <div key={igkey}><span className={'burger-control__'+imgKey}></span>:{props.ingredients[igkey]}</div>
    })        
    //loại bỏ những object ko có giá trị rồi gộp thành mảng
    .reduce((arr, elm) => {
        return arr.concat(elm)
    },[]);
    // check mảng đó có giá trị không
    if(numberIngredients.length <= 0) {
        numberIngredients = <span>0</span>
    }
    return (
        <div className="burger-control">
            <div className="burger-control__title">Adds-on</div>
        {controls.map(ctrl => (
            <BuildControl 
                key={ctrl.label} 
                label={ctrl.label}
                type = {ctrl.type}
                price = {ctrl.price}
                //lấy ingredientAdd từ comp cha dựa theo type trong control
                add ={()=>props.ingredientAdd(ctrl.type)}
                remove={()=>props.ingredientRemove(ctrl.type)}
                disabled={props.disabled[ctrl.type]}/>
        ))}
        <div className="burger-control__checkout p-3">
        <div className="pb-2 burger-control__checkout-title">Checkout!</div>
        <div className="burger-control__checkout-list">
            {numberIngredients}
        </div>
        </div>
        <p className="burger-control__total">Total: <span>{props.price}$</span></p>
        <button className={`btn btn-danger btn-order mt-3 ${!props.purchasable? 'disabled':''}`} disabled={!props.purchasable}>Order Now</button>
        </div>
    );
};


export default BurgerControl;
